const isValidUserMentionRegex = /^[&!]?\d+$/;

function replaceEveryone(_match: string, target: string): string {
	if (isValidUserMentionRegex.test(target)) return `@${target}`;
	else return `@\u200b${target}`;
}

export = ChannelMethods;







